@use './_global' as *

.ldsDualRing
    display: inline-block
    width: 80px
    height: 80px

    &:after
        content: " "
        display: block
        width: 64px
        height: 64px
        margin: 8px
        border-radius: 50%
        border: 6px solid $main-color
        border-color: $secondary-color transparent $main-color transparent
        animation: lds-dual-ring 1.2s linear infinite

.spinner
    display: flex
    justify-content: center
    align-items: center
    height: 100vh
@keyframes lds-dual-ring
    0%
        transform: rotate(0deg)

    100%
        transform: rotate(360deg)
